import { _decorator, Component, Node } from 'cc';
import { wiperLeft } from './wiperLeft';
import { wiperRight } from './wiperRight';
const { ccclass, property } = _decorator;

@ccclass('ysts')
export class ysts extends Component {
    wiperL = null
    wiperR = null
    wiperStatus = true
    wStart = null
    start() {
        this.wiperL = this.node.parent.parent.parent.getChildByName('tuibiji').getChildByName('component').getChildByName('wiper_l.001').getComponent(wiperLeft)
        this.wiperR = this.node.parent.parent.parent.getChildByName('tuibiji').getChildByName('component').getChildByName('wiper_r.001').getComponent(wiperRight)
    }

    wiperswitch() {
        if (Date.now() - this.wStart <= 800) {
            return;
        }
        this.wStart = Date.now()
        if(this.wiperStatus) {
            this.wiperL.wileft.stop()
            this.wiperR.wiRight.stop()
            this.wiperStatus = false
        } else {
            this.wiperL.wileft.start()
            this.wiperR.wiRight.start()
            this.wiperStatus = true
        }
    }

    update(deltaTime: number) {
        
    }
}

